.about {
  background-color: $brand-main-color;
  padding-top: 60px;
}

.about__label {
  display: block;

  font-weight: 700;
  font-size: 18px;
  line-height: calc(22 / 18);
  text-align: center;
  text-transform: uppercase;
  color: $text-light-color;

  margin-bottom: 12px;

  @media screen and (min-width: $tablet) {
    font-weight: 800;
    font-size: 22px;
    line-height: calc(27 / 22);
    letter-spacing: 0.04em;
  }

  @media screen and (min-width: $desctop) {
    font-size: 30px;
    line-height: calc(37 / 30);
  }
}
.about__title {
  display: block;

  font-family: $font-title;
  font-weight: 400;
  font-size: 30px;
  line-height: calc(38 / 30);
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $text-light-color;

  width: 200px;
  margin-left: auto;
  margin-right: auto;

  margin-bottom: 32px;

  @media screen and (min-width: $tablet) {
    font-size: 34px;
    line-height: calc(43 / 34);
    letter-spacing: 0.06em;

    width: 100%;

    margin-bottom: 72px;
  }

  @media screen and (min-width: $desctop) {
    font-size: 36px;
    line-height: calc(45 / 36);

    margin-bottom: 82px;
  }
}
.about__decor {
  display: block;

  position: relative;

  width: 278px;
  height: 278px;

  background: linear-gradient(0deg, $img-decoration, $img-decoration),
    $back-round-decoration;
  box-shadow: 0px 4px 16px $back-round-img-shadow;
  border-radius: 140px;

  margin-left: auto;
  margin-right: auto;
  margin-bottom: 62px; //40+22

  @media screen and (min-width: $tablet) {
    width: 335px;
    height: 330px;

    box-shadow: 0px 5px 20px $back-round-img-shadow;
    border-radius: 165px;

    margin-bottom: 0;
  }
  @media screen and (min-width: $desctop) {
    width: 500px;
    height: 500px;

    box-shadow: 0px 8px 30px $back-round-img-shadow;
    border-radius: 250px;
  }
}
.about__photo {
  position: absolute;

  top: 22px;

  margin-left: auto;
  margin-right: auto;

  @media screen and (min-width: $tablet) {
    top: 26px;
  }
  @media screen and (min-width: $desctop) {
    top: 39px;
  }
}

.about__box {
  @media screen and (min-width: $tablet) {
    display: flex;
    gap: 32px;
  }
  @media screen and (min-width: $desctop) {
    gap: 100px;
  }
}

.about__group {
  @media screen and (min-width: $tablet) {
    padding-top: 7px;
    padding-bottom: 17px;

    @media screen and (min-width: $desctop) {
       padding-top: 90px;
        padding-bottom: 35px;
    }
  }
}
.about__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 4px;

  margin-bottom: 20px;

  @media screen and (min-width: $tablet) {
    align-items: flex-start;

    gap: 8px;

    margin-bottom: 48px;
  }

  @media screen and (min-width: $desctop) {
  }
}

.about__descrip {
  font-weight: 400;
  font-size: 12px;
  line-height: calc(21 / 12);
  text-align: center;
  letter-spacing: 0.04em;
  color: $text-light-color;

  width: 220px;

  @media screen and (min-width: $tablet) {
    width: 334px;
    text-align: left;
  }
  @media screen and (min-width: $desctop) {
    width: 463px;

    font-size: 16px;
    line-height: calc(28 / 16);
  }
}

.about__descrip--hidden {
  @media screen and (max-width: 767px) {
    display: none;
  }
}

.about__button {
  display: block;
  margin-left: auto;
  margin-right: auto;

  font-family: inherit;

  min-width: 164px;
  min-height: 42px;

  background: $brand-second-color;
  border-radius: 10px;
  border: none;

  @media screen and (min-width: $tablet) {
    margin-left: 0;
  }
  @media screen and (min-width: $desctop) {
  }
}
